nav{
    background: rgba(59, 76, 201, 0.3);
    width: max-content;
    display: block;
    padding: 0.7rem 1.7 rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
}

nav a {
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: rgb(69, 236, 209);
    font-size: 1.1 rem;
}
nav a:hover {
    background: rgba(89, 218, 211, 0.3);
}

nav a.activ {
    background: var(--color-primary);
    color: var(--color-bg);
}