header{
    height: 100vm;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    align-items: center;
    height: 100%;
    position: relative;
}

/*===========cta=======*/
.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/*===========Socials=======*/
.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 5rem;
    bottom: 3rem;
}

.header__socials::after {
    content: "";
    width: 1px;
    height: 3rem;
    background: var(--color-primary);
}

/*===========image=======*/
.me {
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}

/*===========scrole=======*/
.scrol__down {
    position: absolute;
    right: 0rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/*================ tabler ===================*/
@media screen and (max-width: 1024px) {
    header{
        height: 68vh;
    }
}

/*================ mobile ===================*/
@media screen and (max-width: 600px) {
    header{
        height: 100vh;
    }
    .header__socials, .scrol__down{
        display: none;
    }
}