.about__container {
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}

.about__me {
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent, var(--color-primary), transparent);
    display: grid;
    place-items: center;
}
 .about__me-image {
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(0deg);
    transition: var(--transition);
 }
 .about__me-image:hover {
    transform: rotate(10deg);
 }
 .about__cards {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 1.5rem;
 }

 .about__card {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
 }
 .about__card:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
 }
 .about__icon {
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
 }

 .about__card h5 {
    font-size: 0.9rem;
 }
 .about__card samll {
    font-size: 0.7rem;
    color: var(--color-light);
 }
 .about__content p {
    margin: 2rem 0 2r.6rem;
    color: var(--color-light);
 }

 /*================ tabler ===================*/
@media screen and (max-width: 1024px) {
   .talk{
      text-align: center;
   }
   .top {
      margin-left: -8rem;
   }
    .about__container{
        grid-template-columns: 1fr;
        gap: 0;
        margin-right: 0rem;
    }
    .about__me {
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about__content p {
      margin: 1rem o 1.5rem;
    }
}

/*================ mobile ===================*/
@media screen and (max-width: 600px) {
   .about__me-image:hover {
      transform: none;
   }
   .about__cards {
      display: grid;
      grid-template-columns: 1fr 1fr;
   }
   .about__me {
      width: 100%;
      margin-top: 0rem;
   }
   .about__cards {
      gap: 3rem;
   }
   .about__content {
      text-align: center;
   }
   .about__content p {
      margin-top:1.3rem;
      margin-left: -1.5rem;
   }
}